.date-container{
    position: relative;
    width: 90%;
    height: 120vh;
    background-color: rgba(45, 167, 167, 0.747);
    display: flex;
    align-items: center;
    flex-direction: column;
    background-image: url('../assets/image.png');
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    background-size: 130%;
    border-radius: .5rem;
    background-color: var(--colorPurple);
}
.title-date{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 20%;
    height: 20%;
}

.title-date > h1{
    font-size: 50px;
    font-weight: bold;
    color: white;
    text-shadow:  1px 10px 5px rgba(71, 194, 157, 0.527), 0 0 1em blue, 0 0 0.2em blue;
}

.place-date-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 5%;
}
.place-date-container> h2{
    color: white;
    margin-left: 2%;
}
.place-text::after{
    content: "";
    position: absolute;
    width: 5px;
    height: 4%; 
    margin-left: 10px;
    background-color: white;
}

/*donde se muestran los numeros*/
.numbers-container-date{
    width: 100%;
    height: 10%;
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: nowrap;
    align-items: center;
}
.box-info-numbers{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: auto;
    max-width: 20%;
    height: 100%;
    color: white;
    margin-left: 10px;
}
.box-numbers{
    font-weight: bold;
    font-size: 25px;
}
.box-text{
    text-align: center;
    color: white;
    min-height: 10%;
}
/*apartado de lista y mapa*/
.details-info-container{
    width: 100%;
    height: 60%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.details , .location{
    width: 45%;
    height: 90%;
    border-radius: .4rem;
    outline: none;
    border: none;
}
.details{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.title-details{
    width: 80%;
    height: 10%;
    color: white;
    margin: 20px;
    font-size: 18px;
}
.details > ul{
    width: 80%;
    color: white;
}
.resaltador{
    font-weight: bold;
    text-decoration: underline;
}