.phamphlet-container{
    display: flex;
    position: relative;
    width: 90%;
    height: 80vh;
    background-color: var(--blueColor);
    margin: 40px ;
    border-radius: 1rem;
    border: solid 1px var(--whiteColorText);
    flex-direction: row;
}

/*contenedor de informacion "preparate!!"*/
.pamphlet-article{
    width: 50%;
    margin: 5px;
    color:var(--whiteColorText);
}
.header-article{
    width: 100%;
    display: flex;
    height: 10%;
    align-items: center;
    justify-content: flex-start;
    font-size: 18px;
    font-weight: bold;
}
.icon-header-article{
    width: 10%;
    height: 100%;
    background-image: url('../../public/favicon.png');
    background-position: center;
    background-repeat: no-repeat;
}
.phamplet-title{
    margin: 20px;
    font-size: 40px;
    font-weight: bold;
    text-align: left;
}
.phamplet-description{
    margin: 20px;
    text-align: left;
    font-size: 20px;
}
.enrollment-button{
    background-color: var(--lightGreen);
    margin: 20px;
    width: 35%;
    height: 10%;
    color: var(--whiteColorText);
    border:solid 1px var(--blueColo);
    border-radius: .5rem;
    font-size: 17px;
}
.enrollment-button:hover{
    position: relative;
    top:-1%;
    cursor: pointer;
    filter:contrast(2);
}
.image-pamphlet{
    margin: 20px;
    border-radius: .5rem;
}