.form-container {
    position: relative;
    width: 90%;
    height: 100vh;
    background-color: var(--blueColor);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    padding: 20px;
    box-sizing: border-box;
  }
  
  form {
    position: relative;
    width: 40%;
    height: 90%;
    border-radius: 0.5rem;
    z-index: 4;
    background-color: white;
    box-shadow: -8px 8px 8px 5px rgba(255, 255, 255, 0.671);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 20px;
    box-sizing: border-box;
  }
  form label , form input , form button{
    margin-left: 30px;

  }
  form label {
    margin-bottom: 10px;
    font-weight: bold;
  }
  
  form input,
  form textarea {
    width: 80%;
    padding: 8px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: .3rem;
    box-sizing: border-box;
    outline: none;
  }
  
  form textarea {
    height: 80px;
  }
  
  form p {
    margin-top: 10px;
    margin-bottom: 15px;
    font-weight: bold;
  }
  
  form button {
    background-color: var(--lightGreen);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    width: 40%;
  }
  
  form button:hover {
    background-color: var(--lightGreen2);
  }
  .red{
    color: red;
  }
.img-form-container{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 20px;
    border-radius: .4rem;
}
.team-taxis-libres{
    height: 50%;
}
.description-img{
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color:var(--whiteColorText)
}

