@media (max-width:1100px) {

    .phamphlet-container,
    .goal-container,
    .prizes-container,
    .date-container,
    .sponsor-container,
    .form-container,
    footer {
        width: 100%;
        margin-left: 0;
    }

    .date-container {
        background-size: 140%;
        background-repeat: repeat;
    }

    .phamplet-title {
        font-size: 30px;
    }

    .phamplet-description {
        font-size: 15px;
    }

    .App {
        overflow-x: hidden;
    }
}

@media (max-width:1050px) {
    .prizes-article {
        width: 50%;
    }

    .target-prize-content {
        min-height: 44%;
    }

    .target-sponsor {
        width: 30%;
    }

    .form {
        width: 46%;
    }
}

@media (max-width:950px) {
    form {
        width: 50%;
    }

    .img-form-container {
        width: 43%;
    }

    .icon-sponsors {
        margin-left: 0;
        width: 100%;
        justify-content: space-around;
    }

    .target-sponsor {
        margin-left: 0;
    }
}

@media (max-width:880px) {
    .image-pamphlet {
        width: 40%;
        object-fit: cover;
    }
}

@media (max-width:780px) {
    .menu {
        display: none;
    }

    .menu-mobile {
        z-index: 10;
    }

    .openNavbar {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .closeNavbar.active{
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 10;
    }
    
}

@media (max-width:740px) {
    .timeline{
        width: 100%;
    }
    .point-content p{
        font-size: 12px;
    }
    .title-goal{
        width: 90%;
    }
    .prizes-article{
        width: 70%;
    }
    .target-prize-content{
        justify-content: flex-start;
        padding-top: 2%;
        width: 24%;
        margin-left: 10px;
        max-height: 45%;
    }
    .target-prize-content::before{
        height: 46%;
    }
    .description-target-prize{
        font-size: 10px;
    }
    .close-targets-prizes{
        width: 8%;
        background-size: 40%;
    }
    .close-targets-prizes:hover{
        background-size: 35%;
    }
    .title-date{
        height: 15%;
    }
    .box-text{
        min-height: 2%;
    }
    .details , .location{
        width: 50%;
    }
    .details > ul{
        width: 95%;
    }
    .place-text::after{
        margin-left: 6px;
    }
    .box-info-numbers{
        width: 20%;
    }
}

@media (max-width:690px) {
    .enrollment-button{
        margin: 0;
        margin-left: 15px;
    }
    .phamplet-description{
        width: 100%;
    }
    .phamplet-title{
        width: 100%;
    }
    .icon-header-article{
        margin: 15px;
    }
    .container-logo-button {
        width: 32%;
    }
}
@media (max-width:690px) {
    .phamphlet-container{
        flex-direction: column;
        height: 100vh;
        justify-content: center;
        align-items: center;
    }
    .pamphlet-article{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        width:80%;
        height: 70%;
    }
    .image-pamphlet{
        width: 80%;
        height: 30%;
    }
    .prizes-container{
        height: 100vh;
    }
    .prizes-article{
        width: 100%;
        background-color: var(--lightGreenTransparency);
    }
    .target-prize-content{
        min-height: 38%;
        width: 30%;
    }
    .target-prize-content::before{
        height: 41%;
        width: 22;
    }
    .target-sponsor{
        width: 32%;
        background-size: 75%;
    }
    .date-container{
        height: 160vh;
    }
    .details-info-container{
        flex-direction: column-reverse;
    }
    .details, .location{
        width: 90%;
        height: 50%;
    }
    .box-text{
        min-height: 5%;
    }
    .form-container{
        height: 150vh;
        flex-direction: column-reverse;
    }
    .form-container , form{
        width: 90%;
    }

    .img-form-container{
        width: 100%;
        border-radius: .5rem;
    }
    .team-taxis-libres{
        width: 100%;
        border-radius: .5rem;
        height: 100%;
    }
    .description-img{
        width: 100%;
    }
    .footer-icon {
        width: 8%;
    }

    .section1-footer > li{
        min-width: 28%;
        font-size: 12px;
    }
}
@media (max-width:550px) {
    .sponsor-container{
        height: 140vh;
        justify-content: flex-start;
        align-items: flex-start;
        margin: 0;
    }
    .date-container , .sponsor-container,.form-container{
        left: -5%;
    }
    .target-sponsor{
        width: 45%;
        height: 30%;
    }
  
    .place-text::after{
        display: none;
    }
    .box-info-numbers{
        width: 35%;
    }

    .target-sponsor{
        height: 25%;
    }
    .sponsor-container{
        height: 90vh;
    }
    .title-details{
        width: 95%;
    }
    .form-container, form{
        width: 98%;
    }
    form input, form textarea , form button{
        height: 9%;
    }
    .menu-mobile.active{
        width: 60%;
    }
}

@media (max-width:490px){
    .container-logo-button{
        width: 50%;
    }
    .pamphlet-article{
        width: 90%;
    }
    .header-article {
        height: 25%;
    }
    .enrollment-button{
        height: 20%;
    }
    .goal-container{
        left: -5%;
        height:80vh;
        width: 95%;
    }
    .goal-description{
        width: 90%;
    }
    .point-content h3{
        font-size: 15px;
    }
    .timeline::before{
        top: -40%;
    }
    .form-container, form{
        width: 100%;
        box-shadow: none;
    }
    footer{
        position: relative;
        width: 100%;
        left: -5%;
    }
    .footer-icon{
        width: 10%;
    }

    .target-prize-content {
        height: 30%;
        width: 31%;
        margin: 0;
    }
    .title-target-prize{
        font-size: 10px;
    }
    .description-target-prize{
        width: 100%;
        font-size: 10px;
    }
}