.goal-container {
    position: relative;
    width: 90%;
    height: 80vh;
    border-radius: .5rem;
    background-color: var(--blueColor);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.title-goal {
    position: relative;
    font-size: 25px;
    font-weight: bold;
    color: var(--whiteColorText);
    width: 40%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.goal-description {
    width: 70%;
    height: 30%;
    font-size: 18px;
    color: var(--whiteColorText);
    text-align: center;
}

/*line time*/
.timeline {
    width: 80%;
    list-style: none;
    padding: 0;
    margin: 0;
    position: relative;
    display: flex;
    justify-content: space-between;
    color: var(--whiteColorText);
    align-items: center;
}

.timeline::before {
    content: "";
    width: 100%;
    height: 100%;
    top:-40%;
    position: absolute;
    border-top: dashed 10px var(--lightGreen);
}

.point {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    flex: 1;
}

.point::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #ccc;
}

.point-content {
    margin-left: 20px;
}

.point-content h3 {
    margin: 0;
    font-size: 18px;
    font-weight: bold;
}

.point-content p {
    margin: 5px 0 0;
    font-size: 15px;
}