.navbar {
  background-color: var(--blueColor);
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  height: 10vh;
  justify-content: flex-end;
  align-items: center;
  z-index: 5;
}
.openNavbar{
  display: none;
  position: absolute;
  float: right;
  right: 0;
  width: 10%;
  right: 5%;
  height: 10vh;
  background-color: transparent;
  border: none;
  background-image: url('../assets/open.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 70%;
  cursor: pointer;
  filter: invert(1);
}
.openNavbar.active{
  display: none;
}

.closeNavbar{
  display: none;
  position: absolute;
  float: right;
  width: 10%;
  right: 1%;
  height: 10vh;
  background-color: transparent;
  border: none;
  background-image: url('../assets/close.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40%;
  cursor: pointer;
  filter: invert(1);
}

.closeNavbar.active{
  display: flex;
  position: fixed;
}
.menu li {
  position: relative;
  margin-right: 16px;
  border-bottom:solid 0px green;
}
.menu li:hover{
  content:"";
  position: relative;
  bottom: 0;
  border-bottom:solid 6px green;
  z-index: 2;
  transition: .2s;
}

.menu a {
  text-decoration: none;
  color: #333333;
  padding: 10px 16px;
  transition: .5s;
  color:var(--whiteColorText);
}

.submenu {
  display: none;
  position: absolute;
  left: 0;
  background-color: var(--blueColor);
  border: 1px solid #dddddd;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  top:150%;
}

.menu li:hover .submenu {
  display: block;
}

.submenu li {
  width: 100%;
}

.submenu a {
  display: flex;
  padding: 8px 16px;
  color: var(--whiteColorText);
  text-decoration: none;
}

.submenu a:hover {
  background-color: greenyellow;
}

.container-logo-button{
  position: relative;
  display: flex;
  width: 20%;
  height: 100%;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}
.logo{
  position: relative;
  display: flex;
  width: 50%;
  left: 5%;
  box-shadow: 2px 2px 2px 5px rgba(0, 0, 0, 0.205);
  border-radius: 1rem;
}
.theme-toggle-btn {
  display: flex;
  background-size: cover; /* Ajusta el tamaño de la imagen según sea necesario */
  background-position: center; /* Ajusta la posición de la imagen según sea necesario */
  /* Agrega cualquier otro estilo necesario para el botón */
  width: 15%;
  object-fit: cover;
  background-color: transparent;
  filter: invert(1);
  border: none;
  cursor: pointer;
}


.theme-toggle-btn:hover {
  transform: scale(1.1,1.1);
}
.theme-toggle-btn > img{
  width: 100%;
}

