    .menu-mobile{
        display: none;
        position: fixed;
        top:0;
        right:  -9999px;
        width: 50%;
        height: 100vh;
        float: right;
        background-color: var(--blueColor);
        border-left: solid 1px white; 
    }
    .menu-mobile.active{
        display: flex;
        animation: open_nav .4s alternate forwards ease;
    }
    .menu-mobile.desactive{
        animation: close_nav 3s alternate forwards ease;
    }
    .navbar{
        height: 10vh;
    }