.prizes-container{
    width: 90%;
    height: 90vh;
    display: flex;
    flex-direction: row;
    position: relative;
    overflow: hidden;
    border-radius: .3rem;
}
.prizes-container::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('../assets/background_transparent.png');
    background-repeat: no-repeat;
    top: -10%;
    left: -20%;
    z-index: 3;
    border-radius: .3rem;
}
.prizes-container::after{
    content: "";
    display: flex;
    position: absolute;
    width: 95%;
    height:90%;
    box-shadow: -20px 5px 4px 20px rgba(0, 0, 0, 0.527);
    z-index: 1;
    border-radius: .3rem;
}
/*informacion de premios */
.prizes-article{
    position: absolute;
    right: 5%;
    float: right;
    width: 45%;
    height: 90%;
    background-color:var(--lightGreen);
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    z-index: 6;
    border-radius: .3rem;
}
.title-prizes{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 70%;
    height: 20%;
    color: var(--whiteColorText);
    font-size: 17px;
    font-weight: bold;
}
.icons-prizes{
    width: 70%;
    height: 12%;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.icons-prizes > span{
    background-position: center;
    background-size: 100%;
    background-repeat: no-repeat;
    width: 20%;
    height: 100%;
    filter: invert(1);
}
.--1{
    background-image: url('../assets/icon-prize/icon1_prize.png');
}
.--2{
    background-image: url('../assets/icon-prize/icon2_prize.png');
}
.--3{
    background-image: url('../assets/icon-prize/icon3_prize.png');
}
.--4{
    background-image: url('../assets/icon-prize/icon4_prize.png');
}
.--5{
    background-image: url('../assets/icon-prize/icon5_prize.png');
}
.btn-show-prizes{
    position: relative;
    width: 30%;
    height: 10%;
    outline: none;
    border:solid 1px var(--lightGreen);
    cursor: pointer;
    border-radius: .3rem;
}
.btn-show-prizes:hover{
    transition: 1s;
    top: -1%;
}
.description-prizes{
    display: flex;
    width: 70%;
    text-align: left;
    color: var(--whiteColorText);
}

.targets-prizes{
    position: relative;
    width: 95%;
    left: 100%;
    height: 95%;
    background-color: var(--blueColor);
    /*    animation: swipe_left 1s alternate forwards ease-in;*/
    animation-delay: 3s;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: 7;
    flex-wrap: wrap;
}
.targets-prizes.ActivarAnimacion {
    animation: swipe_left 1s alternate forwards ease-in;
  }
  .targets-prizes.DesactivarAnimacion {
    animation: swipe_right 1s alternate forwards ease-in;
  }

.target-prize-content{
    width: 20%;
    height: auto;
    min-height: 50%;
    border-radius: .5rem;
    background-color: rgb(124, 202, 121);
    border: solid 1px white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: var(--whiteColorText);
    margin-left: 60px;
}
.target-prize-content::before{
    content: "";
    width:18%;
    height: 55%;
    position: absolute;
    z-index: -1;
    border-radius: .4rem;
}
.target-prize-content:hover::before{
    transition: 1s;
    background-color: rgba(255, 0, 170, 0.959);
}
.img-target-prize{
    width: 40%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    filter: invert(1);
}
.title-target-prize{
    font-size: 15px;
    font-weight: bold;
    width: 100%;
    text-align: left;
    margin-left: 10%;
}
.description-target-prize{
    width: 90%;
    text-align: left;
    font-size: 12px;
}
.close-targets-prizes{
    position: absolute;
    left: 0;
    top: 0;
    width: 5%;
    height: 10%;
    background-image: url('../assets/back_icon.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 60%;
    background-color: var(--lightGreen);
    transform: rotate(180deg);
}
.close-targets-prizes:hover{
    background-size: 90%;
    cursor: pointer;
}


